// Life on Campus page styles
$padding-base: 15px;
$margin-base: 30px;
$margin-sm: 30px;
$margin-md: 60px;

.life-on-campus {
  #events {
    text-align: center;

    .slick-arrow {
      z-index: 10;
      top: 40%;

      &.slick-prev {
        left: 10px;
        &:before {
          font-family: FontAwesome;
          font-size: 36px;

          content: '\f104';

          color: $gray-light;
        }
      }
      &.slick-next {
        right: 10px;
        &:before {
          font-family: FontAwesome;
          font-size: 36px;

          content: '\f105';

          color: $gray-light;
        }
      }
    }

    .events-slide {
      width: 46vw;
      padding: $padding-base*2 0 $padding-base;

      cursor: pointer;
      transition: all $speed-default ease-in-out;

      outline: none;

      &.slick-current {
        background-color: $gray-lighter;
      }

      .date {
        font-family: $font-family-heading;
        font-weight: 500;

        width: 96px;
        height: 96px;
        margin: 0 auto;
        margin-bottom: 4px;
        padding: 12px 0;

        border-radius: 100%;
        background-color: $brand-primary;
        .month {
          font-size: 18px;
        }
        .day {
          font-size: 42px;
          line-height: 1;
        }
      }
      .name {
        display: flex;

        height: 3.6em;
        padding: 0 $padding-base;

        align-items: center;
        justify-content: center;
        h4 {
          text-transform: uppercase;
        }
      }
      .detail {
        p {
          font-size: 14px;

          margin: 0;
        }
      }
    }
  }

  #events-info {
    .events-image {
      margin-bottom: $margin-base;
    }
  }

  #blog {
    .featured-posts {
      margin-top: $margin-base;

      figure {
        img {
          margin-top: $margin-base;

          &.no-margin-top {
            margin-top: 0;
          }
        }

        figcaption {
          padding-right: 0;
          padding-left: 0;

          text-align: left;

          color: $gray-dark;

          i {
            font-family: $font-family-sans-serif;
            font-style: normal;
          }
        }
      }
    }
    .blog-cta {
      margin-top: $margin-base;

      text-align: center;
    }
  }
}

@media (min-width: $screen-xs) {
}

@media (max-width: $screen-xs) {
  .life-on-campus {
    #events {
      .events-slide {
        transform: scale(.8);

        opacity: .5;

        &.slick-center {
          transform: scale(1);

          opacity: 1;
        }
      }
    }
  }
}

@media (min-width: 480px) {
  .life-on-campus {
    #events {
      padding-right: $padding-base;
      padding-left: $padding-base;
    }
  }
}

@media (min-width: $screen-sm) {
  .life-on-campus {
    #events {
      @include xlgCap();
    }
    #events-info {
      @include xlgCap();
      .events-image {
        margin-bottom: 0;
      }
    }
    #blog {
      .featured-posts {
        margin-top: $margin-sm;
        figure {
          img {
            margin-top: 0;
          }
        }
      }
      .blog-cta {
        margin-top: $margin-sm;

        text-align: center;
      }
    }
  }
}

@media (min-width: $screen-md) {
  .life-on-campus {
    #blog {
      .featured-posts {
        margin-top: $margin-md;
      }
      .blog-cta {
        margin-top: $margin-md;

        text-align: center;
      }
    }
  }
}
@media (min-width: $screen-lg) {
}
